"use client"

import { useEffect, useState } from 'react'
import { useRouter } from 'next/navigation'
import Link from 'next/link'
import { Button } from '@/components/ui/button'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'

export default function HomePage() {
  const router = useRouter()
  const [isAuthenticated, setIsAuthenticated] = useState(false)
  const [isLoading, setIsLoading] = useState(true)

  useEffect(() => {
    const token = localStorage.getItem('token')
    const user = localStorage.getItem('user')
    
    if (token && user) {
      setIsAuthenticated(true)
    }
    setIsLoading(false)
  }, [])

  const handleGetStarted = () => {
    if (isAuthenticated) {
      router.push('/dashboard')
    } else {
      router.push('/auth/signup')
    }
  }

  const handleViewDemo = () => {
    if (isAuthenticated) {
      router.push('/leads')
    } else {
      router.push('/auth/login')
    }
  }

  if (isLoading) {
    return (
      <div className="flex items-center justify-center min-h-[400px]">
        <div className="text-center">
          <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-primary mx-auto"></div>
          <p className="mt-2 text-muted-foreground">Loading...</p>
        </div>
      </div>
    )
  }

  return (
    <div className="space-y-16">
      {/* Hero Section */}
      <section className="text-center space-y-6 py-16">
        <h1 className="text-4xl md:text-6xl font-bold tracking-tight">
          Lead Scoring Platform
        </h1>
        <p className="text-xl text-muted-foreground max-w-3xl mx-auto">
          Optimize your sales pipeline with intelligent lead scoring. Analyze, track, and convert your leads more effectively with our advanced scoring algorithms.
        </p>
        <div className="flex flex-col sm:flex-row gap-4 justify-center">
          <Button onClick={handleGetStarted} size="lg">
            {isAuthenticated ? 'View Dashboard' : 'Get Started Free'}
          </Button>
          <Button onClick={handleViewDemo} variant="outline" size="lg">
            {isAuthenticated ? 'Manage Leads' : 'View Demo'}
          </Button>
        </div>
        {!isAuthenticated && (
          <p className="text-sm text-muted-foreground">
            Already have an account?{' '}
            <Link href="/auth/login" className="text-primary hover:underline">
              Sign in here
            </Link>
          </p>
        )}
      </section>

      {/* Features Section */}
      <section className="space-y-8">
        <h2 className="text-3xl font-bold text-center">Key Features</h2>
        <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
          <Card>
            <CardHeader>
              <CardTitle>Smart Scoring</CardTitle>
              <CardDescription>
                Advanced algorithms analyze multiple data points to provide accurate lead scores
              </CardDescription>
            </CardHeader>
            <CardContent>
              <p className="text-sm text-muted-foreground">
                Our scoring system evaluates engagement, company fit, and behavioral patterns to prioritize your best prospects.
              </p>
            </CardContent>
          </Card>

          <Card>
            <CardHeader>
              <CardTitle>Real-time Analytics</CardTitle>
              <CardDescription>
                Monitor your lead pipeline with comprehensive dashboards and insights
              </CardDescription>
            </CardHeader>
            <CardContent>
              <p className="text-sm text-muted-foreground">
                Track conversion rates, score distributions, and pipeline health with interactive charts and metrics.
              </p>
            </CardContent>
          </Card>

          <Card>
            <CardHeader>
              <CardTitle>Lead Management</CardTitle>
              <CardDescription>
                Efficiently organize and track all your leads in one centralized platform
              </CardDescription>
            </CardHeader>
            <CardContent>
              <p className="text-sm text-muted-foreground">
                Add, edit, and manage leads with detailed profiles, notes, and automated score calculations.
              </p>
            </CardContent>
          </Card>
        </div>
      </section>

      {/* Integration Section */}
      <section className="space-y-8">
        <h2 className="text-3xl font-bold text-center">Seamless CRM Integration</h2>
        <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
          <Card>
            <CardHeader>
              <CardTitle className="flex items-center gap-3">
                <div className="w-8 h-8 bg-orange-100 rounded flex items-center justify-center">
                  <span className="text-sm font-bold text-orange-600">H</span>
                </div>
                HubSpot
              </CardTitle>
              <CardDescription>
                Connect your HubSpot CRM for seamless lead synchronization
              </CardDescription>
            </CardHeader>
            <CardContent>
              <p className="text-sm text-muted-foreground">
                Sync contacts, deals, and companies automatically with real-time updates.
              </p>
            </CardContent>
          </Card>

          <Card>
            <CardHeader>
              <CardTitle className="flex items-center gap-3">
                <div className="w-8 h-8 bg-blue-100 rounded flex items-center justify-center">
                  <span className="text-sm font-bold text-blue-600">S</span>
                </div>
                Salesforce
              </CardTitle>
              <CardDescription>
                Integrate with Salesforce to enhance your existing workflow
              </CardDescription>
            </CardHeader>
            <CardContent>
              <p className="text-sm text-muted-foreground">
                Leverage your Salesforce data to power intelligent lead scoring algorithms.
              </p>
            </CardContent>
          </Card>

          <Card>
            <CardHeader>
              <CardTitle className="flex items-center gap-3">
                <div className="w-8 h-8 bg-red-100 rounded flex items-center justify-center">
                  <span className="text-sm font-bold text-red-600">Z</span>
                </div>
                Zoho CRM
              </CardTitle>
              <CardDescription>
                Connect Zoho CRM for comprehensive lead management
              </CardDescription>
            </CardHeader>
            <CardContent>
              <p className="text-sm text-muted-foreground">
                Sync leads, deals, and accounts with automated scoring updates.
              </p>
            </CardContent>
          </Card>
        </div>
      </section>

      {/* CTA Section */}
      <section className="text-center space-y-6 py-16 bg-muted/50 rounded-lg">
        <h2 className="text-3xl font-bold">Ready to Get Started?</h2>
        <p className="text-lg text-muted-foreground max-w-2xl mx-auto">
          Transform your sales process with intelligent lead scoring. Start identifying your best prospects today.
        </p>
        <div className="flex flex-col sm:flex-row gap-4 justify-center">
          <Button onClick={handleGetStarted} size="lg">
            {isAuthenticated ? 'Go to Dashboard' : 'Start Free Trial'}
          </Button>
          {!isAuthenticated && (
            <Button variant="outline" size="lg" onClick={() => router.push('/auth/login')}>
              Sign In
            </Button>
          )}
        </div>
        {!isAuthenticated && (
          <p className="text-sm text-muted-foreground">
            No credit card required • Free 14-day trial • Cancel anytime
          </p>
        )}
      </section>
    </div>
  )
}
